.test {
  background-color: #3e86f7;
  color: white;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  -webkit-user-select: none;
  overflow: hidden;
}

.box {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  left: 10vmin;
  -webkit-filter: drop-shadow(0px 0.5vmin 0px #3253d7);
  filter: drop-shadow(0px 0.5vmin 0px #3253d7);
}

.titlebox {
  display: flex;
  gap: 0.8vmin;
}

.textbox {
  display: flex;
  flex-direction: column;
}

.title {
  position: relative;
  width: 15vmin;
}

.subtitle {
  position: relative;
  width: 29vmin;
}

.image {
  width: 50vmin;
  position: relative;
  left: -13vmin;
}

.animate {
  animation-name: animate;
  animation-duration: 1s;
  animation-fill-mode: backwards;
  -webkit-animation-name: animate;
  -webkit-animation-duration: 1s;
  -webkit-animation-fill-mode: backwards;
}

.animate.t0 {
  animation-delay: 0.2s;
}

.animate.t1 {
  animation-delay: 0.4s;
}

.animate.t2 {
  animation-delay: 0.6s;
}

.animate.t3 {
  animation-delay: 0.8s;
}

.animate.t4 {
  animation-delay: 1s;
}

@keyframes animate {
  0% {
    opacity: 0;
    top: 5vmin;
  }
  100% {
    opacity: 1;
    top: 0vmin;
  }
}
